.dataAnalysis{
    padding:10px;
    .toolbox{
        text-align: center;
        font-size:14px;
        .btn-link{
          color:#409eff;
          cursor:pointer;
          &+.btn-link{
            margin-left:20px;
          }
          &:hover{
            color:#66b1ff;
            text-decoration: underline;
          }
        }
    }
    .mainTable{
        margin:10px auto;
        border-collapse: collapse;
        th,td{
            text-align: center;
            border:1px solid #DCDFE6;
            font-size:14px;
            line-height:24px;
            padding:0 10px;
        }
    }
}